// ── 서브컴포넌트 ─────────────────────────────────────────────────────────────

function SetupStepItem({ item, completed, onClick }: { item: typeof settingItems[0]; completed: boolean; onClick: () => void }) {
  const Icon = item.icon;
  return (
    <button onClick={onClick} style={{ background: "#ffffff", borderRadius: 16, padding: "10px 14px", display: "flex", flexDirection: "row", alignItems: "center", gap: 12, border: "none", cursor: "pointer", textAlign: "left", width: "100%", flexShrink: 0 }}>
      <div style={{ flexShrink: 0 }}>{completed ? <IconComplete /> : <Icon />}</div>
      <div style={{ flex: 1, minWidth: 0 }}>
        <div style={{ fontFamily: "Inter", fontSize: 13, fontWeight: 600, color: "rgb(72,70,70)", letterSpacing: "-0.4px", lineHeight: "1.2" }}>{item.title}</div>
        <div style={{ fontFamily: "Inter", fontSize: 10, fontWeight: 500, color: completed ? "#F06600" : "rgb(145,144,146)", letterSpacing: "-0.3px", lineHeight: "1.3", marginTop: 2, whiteSpace: "nowrap", overflow: "hidden", textOverflow: "ellipsis" }}>
          {completed ? "Complete" : item.description}
        </div>
      </div>
      <div style={{ flexShrink: 0 }}><ChevronRight /></div>
    </button>
  );
}

function InitialCard({ item, onClick }: { item: any; onClick: () => void }) {
  const Icon = item.icon;
  return (
    <div style={{ background: "rgba(255,255,255,0.6)", borderRadius: 32, padding: 16, display: "flex", flexDirection: "column", minWidth: 0, flex: 1 }}>
      <div style={{ display: "flex", alignItems: "center", gap: 8 }}>
        <Icon />
        <span style={{ fontFamily: "Inter", fontSize: 17, fontWeight: 600, color: "rgb(72,70,70)", letterSpacing: "-0.51px", lineHeight: "1.2" }}>{item.title}</span>
      </div>
      <p style={{ fontFamily: "Inter", fontSize: 13, fontWeight: 500, color: "rgb(145,144,146)", letterSpacing: "-0.39px", lineHeight: "1.4", margin: "16px 0 0", flex: 1 }}>
        {item.description}
      </p>
      <button onClick={onClick} style={{ width: "100%", height: 44, borderRadius: 36, border: "none", cursor: "pointer", background: "rgba(143,138,196,0.7)", display: "flex", alignItems: "center", justifyContent: "center", gap: 6, marginTop: 16, flexShrink: 0 }}>
        <span style={{ fontFamily: "Inter", fontSize: 15, fontWeight: 600, color: "#ffffff", letterSpacing: "-0.45px" }}>Setting</span>
        <span style={{ fontSize: 18, color: "#ffffff", fontWeight: 600, lineHeight: 1 }}>+</span>
      </button>
    </div>
  );
}

function CompletedCard({ item, onReset, onEdit }: { item: any; onReset: () => void; onEdit: () => void }) {
  const summary = item.completedSummary;
  const hasColumns = !!summary.columns;

  return (
    <div style={{ background: "rgba(255,255,255,0.6)", borderRadius: 32, padding: 16, display: "flex", flexDirection: "column", gap: 10, minWidth: 0, flex: 1, overflow: "hidden" }}>
      {/* 헤더 */}
      <div style={{ display: "flex", alignItems: "center", gap: 8, flexShrink: 0 }}>
        <IconComplete />
        <span style={{ fontFamily: "Inter", fontSize: 17, fontWeight: 600, color: "rgb(72,70,70)", letterSpacing: "-0.51px", lineHeight: "1.2" }}>{item.title}</span>
      </div>

      {/* 내용 */}
      <div style={{ background: "#ffffff", borderRadius: 12, padding: "10px 12px", display: "flex", flexDirection: "column", gap: 6, flex: 1, overflow: "hidden" }}>
        {hasColumns ? (
          /* Medical History: 두 컬럼 나란히 */
          <div style={{ display: "flex", gap: 16, flex: 1 }}>
            {(summary.columns as any[]).map((col: any, ci: number) => (
              <div key={ci} style={{ flex: 1, display: "flex", flexDirection: "column", gap: 4 }}>
                <div style={{ fontFamily: "Inter", fontSize: 11, fontWeight: 700, color: "rgb(72,70,70)", letterSpacing: "-0.33px", paddingBottom: 4, borderBottom: "1px solid #e8e7ea" }}>
                  {col.heading}
                </div>
                {(col.rows as any[]).map((row: any, ri: number) => (
                  <div key={ri} style={{ display: "flex", gap: 6, padding: "3px 0" }}>
                    {row.label ? (
                      <span style={{ fontFamily: "Inter", fontSize: 11, fontWeight: 600, color: "rgb(120,119,118)", letterSpacing: "-0.33px", minWidth: 72, flexShrink: 0 }}>{row.label}</span>
                    ) : null}
                    <span style={{ fontFamily: "Inter", fontSize: 11, fontWeight: 500, color: "rgb(72,70,70)", letterSpacing: "-0.33px" }}>{row.value}</span>
                  </div>
                ))}
              </div>
            ))}
          </div>
        ) : (
          /* 일반: 헤딩 + 행 테이블 */
          <>
            {summary.heading && (
              <div style={{ fontFamily: "Inter", fontSize: 13, fontWeight: 700, color: "rgb(72,70,70)", letterSpacing: "-0.39px", paddingBottom: 4, borderBottom: "1px solid #e8e7ea", flexShrink: 0 }}>
                {summary.heading}
              </div>
            )}
            <div style={{ display: "flex", flexDirection: "column" }}>
              {(summary.rows ?? []).map((row: any, i: number) => (
                <div key={i} style={{ display: "flex", padding: "5px 0", borderBottom: i < (summary.rows ?? []).length - 1 ? "1px solid #e8e7ea" : undefined, gap: 12 }}>
                  {row.label && (
                    <span style={{ minWidth: 100, flexShrink: 0, fontFamily: "Inter", fontSize: 11, fontWeight: 600, color: "rgb(120,119,118)", letterSpacing: "-0.33px" }}>{row.label}</span>
                  )}
                  <span style={{ fontFamily: "Inter", fontSize: 11, fontWeight: 500, color: "rgb(72,70,70)", letterSpacing: "-0.33px" }}>{row.value}</span>
                </div>
              ))}
            </div>
          </>
        )}
      </div>

      {/* 버튼 */}
      <div style={{ display: "flex", justifyContent: "flex-end", gap: 8, flexShrink: 0 }}>
        <button onClick={onReset} style={{ height: 42, paddingLeft: 24, paddingRight: 18, paddingTop: 6, paddingBottom: 6, borderRadius: 36, background: "#8f8ac4", border: "none", cursor: "pointer", fontFamily: "Inter", fontSize: 17, fontWeight: 600, color: "#ffffff", letterSpacing: "-0.51px", display: "flex", alignItems: "center", gap: 8 }}>
          Reset
          <svg width="20" height="20" viewBox="0 0 24 24" fill="none">
            <path d="M20 8C18.6 5.3 15.9 3.5 12.8 3.5C8.4 3.5 4.8 7.1 4.8 11.5S8.4 19.5 12.8 19.5c3.2 0 5.9-1.8 7.3-4.5" stroke="white" strokeWidth="2.2" strokeLinecap="round" />
            <path d="M20.5 3.5V8.5H15.5" stroke="white" strokeWidth="2.2" strokeLinecap="round" strokeLinejoin="round" />
          </svg>
        </button>
        <button onClick={onEdit} style={{ height: 42, paddingLeft: 24, paddingRight: 18, paddingTop: 6, paddingBottom: 6, borderRadius: 36, background: "#8f8ac4", border: "none", cursor: "pointer", fontFamily: "Inter", fontSize: 17, fontWeight: 600, color: "#ffffff", letterSpacing: "-0.51px", display: "flex", alignItems: "center", gap: 8 }}>
          Edit
          <svg width="20" height="20" viewBox="0 0 20 20" fill="none">
            <path d="M3 17L6.5 15.5L15 7L13 5L4.5 13.5L3 17Z" stroke="white" strokeWidth="1.6" strokeLinejoin="round" />
            <line x1="13" y1="5" x2="15" y2="7" stroke="white" strokeWidth="1.6" strokeLinecap="round" />
          </svg>
        </button>
      </div>
    </div>
  );
}

